const removeAscent = (str: string) => {
  const map = {
    a: '|||||||||',
    e: '|||||||',
    i: '|||||||',
    o: '|||||||||',
    u: '|||||||',
    n: '|',
  };

  for (const pattern in map) {
    str = str.replace(new RegExp(map[pattern], 'g'), pattern);
  }

  return str;
};

export default removeAscent;
